.showAll {
    position: relative;
    width: 100%;
    height: 100%;
    a {
      display: block;
      width: 100%;
      height: 100%;
      color: #000;
      font-style: normal;
      background: url('../images/showAll.png') no-repeat;
      background-size: 100% 100%;
      overflow: hidden;
      .content{
         width: 100%;
         height:100%;
         position: relative;
         .stickyNoteLeft{
            position: absolute;
            top: 9rem;
            left: 0.5rem;
            width: 11.375rem;
            height: 13.125rem;
            box-sizing: border-box;
            padding-left: 2.025rem;
            padding-top: 2.5rem;
            background: url('../images/StickyNote2.png') no-repeat;
            background-size: 100% 100%;
            text-align: center; 
            .world_14{
                font-size: 15px;
                
            }
            p{
                width:85%;
                font-size: 12px;
                line-height: 1.2;
            }
         
            .Ruler{
                position: relative;;
                width: 6.4375rem;
                height: 1.4375rem;
                background: url('../images/Ruler.png') no-repeat;
                background-size:  100% 100%;
                margin-top: 1rem;
                margin-top: 1.7rem;
                margin-left: 1.5rem;
                .score{
                    position: absolute;
                    top: -1.5375rem;
                    left: 50%;
                    width: .6125rem;
                    height: 2rem;
                    font-size: 12px;
                    margin-left: -20%;
                    color: red;
                    background: url('../images/this.png') no-repeat;
                    background-size: 100% 60%;
                    background-position: center bottom;
                }
            }
            
         }
      }
    }
    .stickyNoteRigth{
        position: absolute;
        top: 18.125rem;
        left: 7.9375rem;
        width: 11.375rem;
        height: 13.125rem;
        box-sizing: border-box;
        padding-left: 1.025rem;
        padding-top: 3.5rem;
        background: url('../images/StickyNote.png') no-repeat;
        background-size:  100% 100%; 
        text-align: center;
        .textli{
            position: absolute;
            top: 5rem;
            left: 3rem;
            width: 6.0625rem;
            height: 4rem;
            background: url('../images/testli.png') no-repeat;
            background-size: 100% 100%;
        }
        .world{
            position: absolute;
            bottom: 3rem;
            left: 1rem;
            font-size: 12px;
        }
        .world_14{
            position: absolute;
            top: 3rem;
            left: 4.5rem;
        }
     } 
  }    