.ladder{
    width: 100%;
    height: 100%;
    a {
      display: block;
      width: 100%;
      height: 100%;
      color: #000;
      font-style: normal;
      background: url('../images/ladder.png') no-repeat;
      background-size: 100% 100%;
      overflow: hidden;
     
    }
  } 
@media  screen and (max-height: 800px){
    .ladder a  .content{
        width: 100%;
        height: 100%;
        position: relative;
        .box,.boxScond,.boxThird{
            position: absolute; 
            top: 2.8875rem;
            left: 7.9375rem;  
            width: auto;
            height: 2.25rem;   
        }
        .boxScond{
           top: 6.275rem;
           left:7.9375rem;
        }
        .boxThird{
            top: 8.8rem;
            left:7.9375rem;
        }
        .allYaer{
            position: absolute;
            top: 15.0375rem;
            left: 12.2875rem;
            padding-top: .5625rem;
            box-sizing: border-box;
            width: 5.4375rem;
            height: 3.1875rem;
            font-size: 18px;
            line-height: 1;
        }
        .first,.second,.third{
            position:absolute;
            top: 24.0375rem;
            left:0rem;
            width: auto;
            height: 1.5rem;
        }
        .second{
            top: 28.0375rem;
            left:3rem;
        }
        .third{
            top: 32.0375rem;
            left:6.5rem;
        }
      }
  }
