.Welcome {
  width: 100%;
  height: 100%;
}
.Welcome a {
  display: block;
  width: 100%;
  height: 100%;
  color: #000;
  font-style: normal;
  background: url('../images/Welcome.png') no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
}
@media screen and (max-height: 800px) {
  .Welcome a .content {
    height: auto;
    height: 4.5625rem;
    margin-top: 7.125rem;
  }
  .Welcome a .content p {
    width: 16.875rem;
    -webkit-text-stroke: 1px #000;
    color: #fff;
    font-weight: bolder;
    margin: 0 auto;
    font-size: 23px;
    line-height: 1.5;
  }
}
@media screen and (min-height: 800px) {
  .Welcome a .content {
    height: auto;
    height: 4.5625rem;
    margin-top: 10.125rem;
  }
  .Welcome a .content p {
    width: 16.875rem;
    -webkit-text-stroke: 1px #000;
    color: #fff;
    font-weight: bolder;
    margin: 0 auto;
    font-size: 23px;
    line-height: 1.5;
  }
}
