.ladder {
  width: 100%;
  height: 100%;
}
.ladder a {
  display: block;
  width: 100%;
  height: 100%;
  color: #000;
  font-style: normal;
  background: url('../images/ladder.png') no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
}
@media screen and (max-height: 800px) {
  .ladder a .content {
    width: 100%;
    height: 100%;
    position: relative;
  }
  .ladder a .content .box,
  .ladder a .content .boxScond,
  .ladder a .content .boxThird {
    position: absolute;
    top: 2.8875rem;
    left: 7.9375rem;
    width: auto;
    height: 2.25rem;
  }
  .ladder a .content .boxScond {
    top: 6.275rem;
    left: 7.9375rem;
  }
  .ladder a .content .boxThird {
    top: 8.8rem;
    left: 7.9375rem;
  }
  .ladder a .content .allYaer {
    position: absolute;
    top: 15.0375rem;
    left: 12.2875rem;
    padding-top: 0.5625rem;
    box-sizing: border-box;
    width: 5.4375rem;
    height: 3.1875rem;
    font-size: 18px;
    line-height: 1;
  }
  .ladder a .content .first,
  .ladder a .content .second,
  .ladder a .content .third {
    position: absolute;
    top: 24.0375rem;
    left: 0rem;
    width: auto;
    height: 1.5rem;
  }
  .ladder a .content .second {
    top: 28.0375rem;
    left: 3rem;
  }
  .ladder a .content .third {
    top: 32.0375rem;
    left: 6.5rem;
  }
}
