@media screen and(min-height: 800px){
    .share{
        position: relative;
        width: 100%;
        height: 100%;
        color: #000;
        font-style: normal;
        background: url('../images/share.png') no-repeat;
        background-size: 100% 100%;
        overflow: hidden;
        header,section{
            position: absolute;
            top: 25.9375rem;
            left:1rem;
            width: 100%;
            font-size: .75rem;
            line-height: 1;
            p{
                width: 100%;
                height:.75rem; 
                text-align: left;
            }
        }
        section{
          width:100%;
          height: 4.125rem;
          position: absolute;
          top: 29.5625rem;
          left: 0  ;
          .newYear{
              position: absolute;
              top: -3rem;
              left: 3rem;
              font-weight: bold;
              font-size: .9375rem;
          }
          img{
            width: 14.4375rem;
            height: 4.125rem;
            }
          ul{
            width: 100%;
            height: 1rem;
            margin-top: 5px;
            padding-left: 2.5rem;
            li{
                float: left;
                width: 4.875rem;
                height: .75rem;
                font-size: .75rem;
                
            }
            li:last-child{
                width: 5.875rem;
            }
          }
        }
        footer{
          position: absolute;
          left:0rem;
          bottom: 0rem;
          width: 100%;
          height: 5.375rem;
          button{
              position: absolute;
              top: 0;
              left: 50%;
              width: 11.375rem;
              height: 2.375rem;
              font-size: 1.125rem; 
              margin-left: -5.6875rem;
              background: url(../images/LuckDram.png) no-repeat;
              background-size: 100% 100%;
              z-index: 999;
          }
          .shareGo{
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 4.125rem;
            background: url(../images/shareBtn.png) no-repeat;
            background-size: 100% 100%;
            z-index: 10;
          }
        }
      }
}
