@media screen and(max-height:800px){
    .star{
        position: relative;
        width: 100%;
        height: 100%;
        a {
          display: block;
          width: 100%;
          height: 100%;
          color: #000;
          font-style: normal;
          background: url('../images/Star.png') no-repeat;
          background-size: 100% 100%;
          overflow: hidden;
          header,footer,section{
              position: absolute;
              top: 7.875rem;
              left:5.8125rem;
              font-size: 14px;
              line-height: 1;
              
              p{
                  width: 11.9375rem;
                  height:3.9375rem; 
                  text-align: left;
              }
          }
          section{
            width: 9.875rem;
            height: 8rem;
            position: absolute;
            top: 11.55rem;
            left: 2.8125rem  
          }
          footer{
            position: absolute;
            top: 19.3125rem;
            left:6.8625rem;
            .showStar{
                font-size: 12px;
                img{
                    width: 0.925rem;
                    height: 0.925rem;
                }
            }
          }
        }
      } 
}
