.yearDetail {
  position: relative;
  width: 100%;
  height: 100%;
}
.yearDetail a {
  display: block;
  width: 100%;
  height: 100%;
  color: #000;
  font-style: normal;
  background: url('../images/yearDetailed.png') no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
}
@media screen and (max-height: 800px) {
  #myChart {
    position: absolute;
    left: 1rem;
    top: 15.125rem;
  }
  #mountNode {
    position: absolute;
    top: 15.825rem;
    left: 2.5rem;
  }
}
@media screen and (min-height: 800px) {
  #myChart {
    position: absolute;
    top: 18.125rem;
    left: 1rem;
  }
  #mountNode {
    position: absolute;
    top: 18.825rem;
    left: 2.5rem;
  }
}
@media screen and (min-height: 823px) {
  #myChart {
    position: absolute;
    top: 17.125rem;
    left: 1rem;
  }
  #mountNode {
    position: absolute;
    top: 17.825rem;
    left: 2.5rem;
  }
}
