.AppIndex {
  width: 100%;
  height: 100%;
  a {
    display: block;
    width: 100%;
    height: 100%;
    color: #000;
    font-style: normal;
    background: url('../images/Appindex.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    
  }
}   
@media screen and (max-height: 800px){
  .AppIndex a .content{
    height: auto;
    height: 4.5625rem;
    margin-top: 22.4375rem;
    margin-left: -3.5rem;
    h2{
      font-size: 28px;
      font-weight: bold;
      line-height: 1.5;
    }
  }
}
@media screen and (min-height: 800px){
  .AppIndex a .content{
    height: auto;
    height: 4.5625rem;
    margin-top: 27.4375rem;
    margin-left: -3.5rem;
    h2{
      font-size: 28px;
      font-weight: bold;
      line-height: 1.5;
    }
  }
}
@media screen and (min-height: 823px){
  .AppIndex a .content{
    height: auto;
    height: 4.5625rem;
    margin-top: 25.4375rem;
    margin-left: -3.5rem;
    h2{
      font-size: 28px;
      font-weight: bold;
      line-height: 1.5;
    }
  }
}