/*全局样式*/
*{padding:0;margin:0;}
div,dl,dt,dd,form,h1,h2,h3,h4,h5,h6,img,ol,ul,li,table,th,td,p,span,a{padding:0;margin:0;border:0;}
img,input{border:none;vertical-align:middle;}
body{width: 100%; height: 100%;font-family:"微软雅黑";font-size:12px;text-align:center;color:#000;}
html{width: 100%; height: 100%; overflow-y:scroll;}
ul,ol{list-style-type:none;}
th,td,input{font-size:12px;outline:none;}
h3{font-size:14px;}
button{border:none;cursor:pointer;font-size:12px;background-color:transparent; cursor: pointer}
select{border-width:1px;_zoom:1;border-style:solid;padding-top:2px;font-size:12px;}
.clear{clear:both;font-size:1px;height:0;visibility:hidden;line-height:0;}
.clearfix:after{content:"";display:block;clear:both;}
.clearfix{zoom:1;}
a:link,a:visited{text-decoration:none;color:#333;}
a:hover,a:active{text-decoration:none;color:#f60;}
.none{display: none;}
#root{width: 100%; height: 100%;}
.root{width: 100%; height: 100%;}

