@media screen and (max-height: 800px) {
  .girlPay {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .girlPay a {
    display: block;
    width: 100%;
    height: 100%;
    color: #000;
    font-style: normal;
    background: url('../images/gilrPay.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
  }
  .girlPay a header,
  .girlPay a footer,
  .girlPay a section {
    position: absolute;
    top: 3.5rem;
    left: 4.0625rem;
    font-size: 16px;
    line-height: 1.5;
  }
  .girlPay a header h1,
  .girlPay a footer h1,
  .girlPay a section h1 {
    font-size: 16px;
    font-weight: normal;
    margin: 0;
  }
  .girlPay a section {
    widows: 9.875rem;
    height: 8rem;
    position: absolute;
    top: 7.75rem;
    left: 0.5125rem;
  }
  .girlPay a footer {
    position: absolute;
    top: 18.5125rem;
    left: 4.0625rem;
  }
}
